.toggle:after {
  content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#ededed" stroke-width="3"/></svg>');
  transform: scale(0.7);
  display: block;
  margin: 0 0 0 -1em;
}

.toggle:checked:after {
  content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#bddad5" stroke-width="3"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>');
  transform: scale(0.7);
  display: block;
  margin: 0 0 0 -1em;
}

.styled-check {
  display: none;
  cursor: pointer;
}

.styled-check + .arrow:before {
  position: absolute;
  content: "";
  display: block;
  top: 25%;
  left: 30%;
  border-left: black 1px solid;
  border-bottom: black 1px solid;
  transform: rotateZ(-135deg);
  width: 0.5em;
  height: 0.5em;
  transition: transform 0.3s ease-in-out;
}

.styled-check:checked + .arrow:before {
  transform: rotateZ(-45deg);
}

.arrow {
  display: block;
  position: relative;
  height: 20px;
  width: 20px;
}

.project > .bodybox {
  font-weight: 600;
}

.bodybox {
  padding: 0.5em;
  display: inline-block;
  white-space: pre-line;
  word-break: break-word;
  display: flex;
  flex-direction: column;
}

.selected {
  background: rgba(245, 215, 110, 0.5);
}

.editing .selected {
  background: white;
}

.editing .body-div {
  flex-direction: column;
  align-items: flex-start;
  padding: 0.4em 0;
}

.done .bodybox {
  color: #d9d9d9;
  text-decoration: line-through;
}

.item-tags {
  white-space: nowrap;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  font-size: 0.7em;
}

.item-tags > span, .item-tags > label {
  align-items: center;
  display: flex;
  position: relative;
  padding: 0.1em 0.5em;
  margin: 0.1em;
  border-radius: 0.3em;
  color: white;
}

.item-tags .done {
  cursor: pointer;
  color: #5a5a5a;
  background: #e6e6e6;
}

.item-tags .duration, .item-tags .currentDuration {
  background: #5dacce;
}

.item-tags .duedate {
  background: #e65e4d;
}

.item-tags .duedate-parent {
  background: rgba(230, 94, 77, 0.5);
}

.item-tags .total-duration {
  background: #4679a7;
}

.item-tags .custom-tag {
  background: #5dc2af;
  cursor: pointer;
}

.item-tags button {
  color: white;
  margin-left: 0.5em;
  padding: 0;
}

.completion-notes {
  color: #a2999a;
  padding: 2px 0 0 2em;
  width: 100%;
}

.journal-wrapper > .children > div:first-child .title {
  border-top: none;
  margin-top: 0;
}

.journal-wrapper .title {
  border-top: 1px solid #ededed;
  color: #46c5b6;
  font-size: 1.2em;
  text-align: left;
  padding: 0.5em 0;
  margin-top: 0.7em;
}

.journal-wrapper .item-card {
  align-items: baseline;
}

.journal-wrapper .bodybox {
  color: inherit;
  text-decoration: none;
}

.journal-wrapper .body-div {
  border-bottom: none;
}

.journal-wrapper .journal-date-small {
  color: #dadada;
  font-size: 0.8em;
}

.alert {
  background: #ddd;
  padding: 1em;
}

.alert-success {
  background: lime;
}

.alert-error {
  background: salmon;
}

#popups {
  top: 0;
  position: fixed;
  width: 100%;
  z-index: 5;
  display: flex;
  flex-direction: column;
  background: rgba(248, 248, 248, 0.95);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1), 0 1px 2px rgba(0, 0, 0, 0.1);
}

.popup {
  padding: 0.6em;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.popup .top {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 0.5em;
}

.popup .title {
  font-weight: 600;
  margin: 0 auto;
}

.popup .body {
  display: flex;
  flex-grow: 1;
}

.popup input, .popup textarea {
  font-family: 'Calibri', 'Lato';
  color: #4d4d4d;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.4em;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  font-smoothing: antialiased;
  resize: none;
  padding: 0.3em;
  margin-right: 1em;
  background: none;
  border: 1px solid #929292;
  border-radius: 0.3em;
}

.popup label.done-after-done {
  cursor: pointer;
  font-weight: inherit;
  border-bottom: 1px solid;
}

.popup label.done-after-done input {
  border: none;
}

.popup .completion-memo {
  display: flex;
  flex-direction: column;
  flex-grow: 2;
  margin-right: 0.3em;
}

.close-button {
  padding-left: 0.5em;
  font-size: 2em;
  color: black;
}

.popup .used-time {
  display: flex;
  flex-direction: column;
  flex-grow: 0;
}

.popup .used-time span {
  font-size: 1.4em;
  margin-left: 1em;
}

.popup .used-time > div > div {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.popup .used-time button {
  padding: 0.3em 0.3em 0;
}

#popouts-mask {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 4;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity .3s ease;
}

.popout {
  z-index: 5;
  background: white;
  border-radius: 0.3em;
  margin: 1em;
}

.popout .body {
  display: flex;
  flex-direction: column;
}

.popout .toggle-div {
  width: 2em;
  height: 1em;
  margin-top: -4em;
}

.popout .bodybox {
  padding: 1em;
  border-bottom: 1px solid #e3e3e3;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.1em;
  padding: 1em;
}

.popout .confirm-delete .bodybox {
  padding: 1em 2em;
}

.popout .timer-time {
  font-size: 4em;
  margin: 0 auto;
  padding: 0.8em;
}

.popout .nav {
  padding: 0.6em 1em;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.popout button {
  font-size: 1.1em;
  padding: 0 0.4em;
  margin: 0 0.3em;
  margin-bottom: 1px;
}

.popout button:focus {
  margin-bottom: 0;
}

.popout button.play, .popout button.pause {
  font-size: 1.3em;
}

.fix-timer #timer-area {
  background: #f5f5f5;
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
  left: 0;
  padding: 0.5em 1.5em 0;
}

#timer-area {
  margin-bottom: 0.6em;
}

#timer-area .timer {
  display: flex;
  align-items: baseline;
  font-size: 1.3em;
  padding-bottom: 0.5em;
}

#timer-area .timer .timer-time {
  margin-right: 1em;
  margin-left: auto;
}

#timer-area .timer .nav i {
  font-size: 1.5em;
}

.fix-timer #timer-area + div {
  margin-top: 3em;
}

input.flatpickr {
  z-index: -2;
  position: absolute;
}

.flatpickr-wrapper.open {
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  bottom: 0 !important;
  right: 0 !important;
}

.flatpickr-wrapper.open .flatpickr-calendar {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  bottom: 0 !important;
  right: 0 !important;
  margin: auto !important;
  height: 300px !important;
}

.flatpickr-wrapper.open .arrowTop:after, .flatpickr-wrapper.open .arrowTop:before {
  display: none;
}

.journal-wrapper .title {
  color: #46c5b6;
  font-size: 1.2em;
  text-align: left;
}

.journal-wrapper .journal-date-small {
  color: #dadada;
  font-size: 0.8em;
}

.journal-wrapper .completion-notes {
  color: #a2999a;
  padding: 2px 0 0 2em;
}
